            <p class="text">Компания ООО «Аваконт» занимается любыми производственными объемами фрезерной обработки и имеет возможности для решения большого спектра задач: от изготовления уникальных единичных товаров до серийного производства однотипных изделий.</p>
            <img src="../../../public/images/activity/processing.jpg" class="descr_img" alt="Обработка_дерева,_металла_и_пластика_на_фрезерном_станке_ЧПУ"  rel="preload">
            <p class="text">В производственном цехе наша компания имеет <u>фрезерно-гравировальный станок FZT-1212.</u>Сфера применения станка практически не ограничена, он оснащен современной системой ЧПУ, что предоставляет возможность обрабатывать самые разнообразные материалы: пластик, фанера, дерево, цветные металлы.</p>
            <p class="text">Фрезерный обрабатывающий центр ЧПУ позволяет изготавливать изделия любой сложности высокого качества. С помощью этого станка наши сотрудники изготавливают 3D детали машин, фигурные шайбы, корпуса, элементы декора, фигурные объемные буквы, алюминиевые таблички, вывески, декоративные изделия из дерева и многое другое.</p>
            <p class="text clear">Работа на фрезерно-гравировальном станке с ЧПУ основывается на микропроцессорах и операционных системах, а собственные микроконтроллеры системы ЧПУ управляют приводами. Благодаря полной автоматизации процесса, мы добиваемся самой точной обработки разнообразных материалов: снижаем до минимума вероятность брака, что увеличивает производительность работы.</p>
